import NavBar from './components/nav'
import Loader from './components/loader'
import HeroSection from './components/hero'
import HomeProjects from './components/homeProjects'
import Menu from './components/menu'
import {useState, useEffect} from 'react'

const App = () => {

  const [fontSize, setFontSize] = useState(0)
  const [burgerOpen, setBurgerState] = useState(false)
  const root = document.querySelector('#root')

  const listenBurger = () => {
    if(burgerOpen){
      root.classList.add('no-scroll')
    } else {
      root.classList.remove('no-scroll')
    }
  }

  useEffect(() => {
    const html = document.querySelector('html')
    const font = window.getComputedStyle(html).getPropertyValue('font-size')
    setFontSize(font)
    console.log(`FONT SIZE: ${fontSize}`)
    listenBurger()
  }, [])

  return [
    <Loader/>,
    <NavBar navOpen={burgerOpen} setNavOpen={setBurgerState}/>,
    <HeroSection/>,
    <HomeProjects fontSize={fontSize}/>,
    <Menu fontSize={fontSize} navOpen={burgerOpen}/>
  ];
}

export default App;
